@mixin flex-center{
  display:flex;
  align-items: center;
  justify-content: center;
}
@mixin flex-between{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin flex-column{
  display: flex;
  flex-direction: column;
}

@mixin flex-wrap-between{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}